@charset 'utf-8';
@import "dist/button";
@import "mixins/normalize","mixins/reset";
@import "services/view","services/header";

.logo{background: url(../img/logo.png) no-repeat center center ;width: 110px;height: 76px; display: block; margin: 1em auto;background-size: contain;}
.login-page{
	padding: 0px 40px;
	.item{
		position: relative;margin: 25px 0px;
		input{border: none;border-bottom: 1px solid #fff;height: 45px; width: 100%; padding-left: 45px;background:transparent;
			&:focus{outline: none;border: none;border-bottom: 1px solid #fff;}
		}
		i{
			display: block; width: 18px; height: 17.5px; position: absolute; left: 14px; top: 14px;
			&.icon-fd-person{background: url(../img/icon/icon-fd-person.png) no-repeat;background-size: contain;}
			&.icon-fd-lock{background: url(../img/icon/icon-fd-lock.png) no-repeat;background-size: contain;}
		}
		&.error{
			height: 26px;line-height: 26px;display: none;
    		img{float: left;}
			span{color: #b81c2d;}
		}
	}
	#loginBtn{@extend .btn;@extend .btn-transparent;}
	.find-pwd{color: #fff; float: right; font-size: 14px; margin-top: 10px;}
}