@charset 'utf-8';
@import "../mixins/common";
.view{
	min-width: 300px;max-width: 640px;background-color: #fff;overflow: hidden;min-height: 100%;position: relative;
	.no-data{width: 80%; margin: -40% auto 0; display: block; position: absolute; top: 50%; bottom: 0px; left: 0px; right: 0px;}
}
.view-green-background{background:$view-green-background;}
.view-gray-background{background: $view-gray-background-color;}

.exome-menu{
	@extend .view;position: fixed; left: 0px; right: 0px; top: 0px; margin: 0 auto; height: 100%; min-height: calc(100% - 125px); overflow-y: auto; display: none; background-color: #fff; z-index: 4; 
	ul{
		padding-bottom: 125px;overflow: hidden;
		li.replace-li{
			@include attr-calc(width,"100% / 3");
			&:nth-child(2n+1){@include attr-calc("margin-left","100% / 9");@include attr-calc("margin-right","100% / 18");}
			&:nth-child(2n){@include attr-calc("margin-left","100% / 18");@include attr-calc("margin-right","100% / 9");}
		}
	}
}